import * as React from "react"
import SEO from "../../components/seo";
import SyntaxHighlight from "../../components/SynaxHighlight";

const Globals = () => {
    return (
        <>
            <SEO title={ "Globals" }/>
            <h1>Globals</h1>
            <p>You can modify any global variable before the initial @use. Variables must be separated by commas <strong>not</strong> semi-colon. <br/> These variables are used across the whole library.</p>
            <SyntaxHighlight lang={ "scss" } codeString={ `@use "~skil3e-css/src/globals" with (
  $variable: value,
  ...
);
@use "~skil3e-css/src/skil3e";` }/>
            <h3>List of global variables:</h3>
            <SyntaxHighlight lang={"scss"} codeString={`/* Breakpoints */
$phone               : 480px !default;
$tablet              : 768px !default;
$laptop              : 992px !default;
$desktop             : 1200px !default;

/* Colors */
$gray-hue            : 220 !default;
$black               : hsl($gray-hue, 10%, 0%) !default;
$gray-5              : hsl($gray-hue, 10%, 5%) !default;
$gray-10             : hsl($gray-hue, 10%, 10%) !default;
$gray-15             : hsl($gray-hue, 10%, 15%) !default;
$gray-20             : hsl($gray-hue, 10%, 20%) !default;
$gray-30             : hsl($gray-hue, 10%, 30%) !default;
$gray-40             : hsl($gray-hue, 10%, 40%) !default;
$gray-50             : hsl($gray-hue, 10%, 50%) !default;
$gray-60             : hsl($gray-hue, 10%, 60%) !default;
$gray-70             : hsl($gray-hue, 10%, 70%) !default;
$gray-80             : hsl($gray-hue, 10%, 80%) !default;
$gray-85             : hsl($gray-hue, 11%, 85%) !default;
$gray-90             : hsl($gray-hue, 10%, 90%) !default;
$gray-95             : hsl($gray-hue, 10%, 95%) !default;
$white               : hsl($gray-hue, 10%, 100%) !default;

$primary             : hsl(190, 65%, 40%) !default;
$secondary           : hsl(190, 30%, 50%) !default;
$danger              : hsl(0, 70%, 50%) !default;
$success             : hsl(150, 70%, 30%) !default;
$warning             : hsl(30, 100%, 50%) !default;


/* Typography */
$systemFonts         : -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol" !default;
$bodyFont            : $systemFonts !default;
$headingFont         : $systemFonts !default;
$headingLineHeight   : 1 !default;
$headingFontWeight   : 500 !default;
$paragraphLineHeight : 1.5 !default;
$highlight           : hsla(213, 80%, 50%, 0.5) !default;

/* Font sizes */
$h1                  : 2rem !default;
$h2                  : 1.75rem !default;
$h3                  : 1.5rem !default;
$h4                  : 1.25rem !default;
$h5                  : 1.125rem !default;
$h6                  : 1rem !default;
$small               : .75rem !default;`} />
        </>
    )
}

export default Globals
